import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  DistrictLocationItem,
  FC_getLocations,
  LocationAPI,
  ProvinceLocationItem,
  SectorLocationItem,
} from "../../actions";
import { StoreState } from "../../reducers";
import { validateEmail } from "../../utils/validation";
import {
  FC_UpdateProfile,
  FC_UpdateProfile_Interface,
} from "../../actions/auth";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import FormSelectCustom from "../../components/FormSelectCustom/FormSelectCustom";
// props for the component
interface AppProps {
  auth: Auth;
  close: Function;
  FC_UpdateProfile: Function;
}
enum ErrorTarget {
  email,
  phone_number,
  main__error,
  main_success,
  none,
  province_name,
  district_name,
  sector_name,
}
interface AppState {
  loading: boolean;
  error: {
    target: ErrorTarget;
    msg: string;
  };
  all_locations: LocationAPI;
  phone_number: string;
  email: string;
  province_id: string;
  province_name: string;
  district_id: string;
  district_name: string;
  sector_id: string;
  sector_name: string;
  loading_location: boolean;
  select_province: boolean;
  select_district: boolean;
  select_sector: boolean;
}

class _EditProfileDetails extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_location: false,
      error: {
        target: ErrorTarget.none,
        msg: "",
      },
      all_locations: {
        districts: [],
        provinces: [],
        sectors: [],
      },
      phone_number: this.props.auth.user.phone_number,
      email: this.props.auth.user.email,
      province_id: this.props.auth.user.residence?.province_id,
      province_name: this.props.auth.user.residence?.province_name,
      district_id: this.props.auth.user.residence?.district_id,
      district_name: this.props.auth.user.residence?.district_name,
      sector_id: this.props.auth.user.residence?.sector_id,
      sector_name: this.props.auth.user.residence?.sector_name,
      // modals
      select_province: false,
      select_district: false,
      select_sector: false,
    };
  }

  handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({
      ...this.state,
      error: {
        target: ErrorTarget.none,
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  componentDidMount = () => {
    FC_getLocations((success, locations, msg) => {
      if (success === true && locations !== null) {
        this.setState({
          all_locations: locations,
          loading_location: false,
        });
        console.log({ locations });
      } else {
        this.setState({
          loading_location: false,
          error: {
            target: ErrorTarget.main__error,
            msg: msg,
          },
        });
      }
    });
  };

  openProvinceModal = () => {
    this.setState({
      select_province: !this.state.select_province,
    });
  };

  openDistrictModal = () => {
    this.setState({
      select_district: !this.state.select_district,
    });
  };

  openSectorModal = () => {
    this.setState({
      select_sector: !this.state.select_sector,
    });
  };

  updateAccountHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (this.state.email.length <= 4 || !validateEmail(this.state.email)) {
      return this.setState({
        error: {
          target: ErrorTarget.email,
          msg: "Please enter a email",
        },
      });
    }

    // check for errors
    if (this.state.phone_number.length <= 4) {
      return this.setState({
        error: {
          target: ErrorTarget.phone_number,
          msg: "Phone number is required",
        },
      });
    }

    if (this.state.province_name.length <= 4) {
      return this.setState({
        error: {
          target: ErrorTarget.province_name,
          msg: "Please select a province",
        },
      });
    }

    if (this.state.district_name.length <= 4) {
      return this.setState({
        error: {
          target: ErrorTarget.district_name,
          msg: "Please select a district",
        },
      });
    }

    if (this.state.sector_name.length <= 4) {
      return this.setState({
        error: {
          target: ErrorTarget.sector_name,
          msg: "Please select a sector",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: ErrorTarget.none,
        msg: "",
      },
    });

    this.props.FC_UpdateProfile(
      {
        email: this.state.email,
        phone_number: this.state.phone_number,
        user_id: this.props.auth.user.user_id,
        residence: {
          province_id: this.state.province_id,
          province_name: this.state.province_name,
          district_id: this.state.district_id,
          district_name: this.state.district_name,
          sector_id: this.state.sector_id,
          sector_name: this.state.sector_name,
        },
      } as FC_UpdateProfile_Interface,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            error: {
              target: ErrorTarget.main_success,
              msg: "Your profile details updated successfully",
            },
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: ErrorTarget.main__error,
              msg: msg,
            },
          });
        }
      }
    );
  };

  render() {
    return (
      <>
        <Modal
          widthSizeClass={ModalSize.medium}
          backDrop={true}
          backDropClose={false}
          close={() => this.props.close()}
          displayClose={true}
          theme={Themes.default}
          title={
            <label className="text-2xl m-0 text-bold text-gray-700">
              Edit profile
            </label>
          }
          padding={{ body: true, footer: true, title: true }}
        >
          <div className="-mt-3">
            <div className="p-1 rounded-md text-gray-500 text-sm">
              You can only update the Email and the phone number, other info are
              poulled from NIDA database
            </div>
          </div>

          <div className="bg-white rounded-b-md ">
            <form onSubmit={this.updateAccountHandler}>
              {this.state.error.target === ErrorTarget.main_success && (
                <div className="p-2 m-2 text-center rounded-md bg-green-200 text-green-700 animate__zoomIn animate__animated">
                  {this.state.error.msg}
                </div>
              )}

              {this.state.error.target === ErrorTarget.main__error && (
                <div className="p-2 m-2 text-center rounded-md bg-red-200 text-red-700 animate__zoomIn animate__animated">
                  {this.state.error.msg}
                </div>
              )}

              <div className="mt-2">
                <div className="p-2 ">
                  <label htmlFor="title" className="d-block">
                    Email
                  </label>
                  <input
                    type="email"
                    name="email"
                    value={this.state.email}
                    className={`font-bold input-white w-20 inline-block  ${
                      this.state.error?.target === ErrorTarget.email
                        ? "border-red-500 border-2"
                        : "border-gray-500 border-2"
                    }`}
                    placeholder="myemail@email.com"
                    onChange={this.handleChange}
                    disabled={this.state.loading}
                    required
                  />
                  {this.state.error?.target === ErrorTarget.email && (
                    <span className="text-red-600">
                      {this.state.error?.msg}
                    </span>
                  )}
                </div>
              </div>

              <div className="mt-2">
                <div className="p-2 ">
                  <label htmlFor="title" className="d-block">
                    Phone number
                  </label>
                  <input
                    type="text"
                    name="phone_number"
                    value={this.state.phone_number}
                    className={`font-bold input-white w-20 inline-block  ${
                      this.state.error?.target === ErrorTarget.phone_number
                        ? "border-red-500 border-2"
                        : "border-gray-500 border-2"
                    }`}
                    placeholder="0700000000"
                    onChange={this.handleChange}
                    disabled={this.state.loading}
                    required
                  />
                  {this.state.error?.target === ErrorTarget.phone_number && (
                    <span className="text-red-600">
                      {this.state.error?.msg}
                    </span>
                  )}
                </div>
              </div>

              {this.state.loading_location === true ? (
                <div className="p-5 bg-gray-300 text-center text-xl">
                  <span className="animate__animated animate__fadeIn animate__infinite">
                    Loading...
                  </span>
                </div>
              ) : (
                <div className="p-2 bg-gray-100 rounded">
                  <div className="">
                    <div className="text-xl text-gray-700 font-bold mx-2">
                      Residence information
                    </div>
                    <div className="p-2 ">
                      <label htmlFor="title" className="d-block">
                        Province
                      </label>
                      {this.state.select_province === false ? (
                        <input
                          type="text"
                          readOnly
                          value={this.state.province_name}
                          className={`font-bold input-white w-20 inline-block  ${
                            this.state.error?.target ===
                            ErrorTarget.province_name
                              ? "border-red-500 border-2"
                              : "border-gray-500 border-2"
                          }`}
                          // placeholder="Select province"
                          onClick={this.openProvinceModal}
                          // required
                        />
                      ) : (
                        <FormSelectCustom
                          click={(data: ProvinceLocationItem) => {
                            this.setState({
                              select_province: false,
                              province_name: data.province_name,
                              province_id: data.province_code,
                              district_id: "",
                              district_name: "",
                              sector_name: "",
                              sector_id: "",
                              select_sector: false,
                              select_district: false,
                            });
                          }}
                          data={this.state.all_locations.provinces}
                          close={() => {
                            this.setState({
                              select_province: false,
                            });
                          }}
                          id="province_code"
                          title="province_name"
                          loading={this.state.loading_location}
                        />
                      )}
                      {this.state.error?.target ===
                        ErrorTarget.province_name && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  </div>

                  <div className="p-2 ">
                    <label htmlFor="title" className="d-block">
                      District
                    </label>
                    {this.state.select_district === false ? (
                      <input
                        type="text"
                        readOnly
                        value={this.state.district_name}
                        className={`font-bold input-white w-20 inline-block  ${
                          this.state.error?.target === ErrorTarget.district_name
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder=""
                        onClick={this.openDistrictModal}
                        disabled={
                          this.state.province_name === "" ? true : false
                        }
                        required
                      />
                    ) : (
                      <FormSelectCustom
                        click={(data: DistrictLocationItem) => {
                          this.setState({
                            select_district: false,
                            district_name: data.district_name,
                            district_id: data.district_code,
                            sector_name: "",
                            sector_id: "",
                            select_sector: false,
                            select_province: false,
                          });
                        }}
                        data={this.state.all_locations.districts.filter(
                          (itm) => itm.province_code === this.state.province_id
                        )}
                        close={() => {
                          this.setState({
                            select_district: false,
                          });
                        }}
                        id="district_code"
                        title="district_name"
                        loading={this.state.loading_location}
                      />
                    )}
                    {this.state.error?.target === ErrorTarget.district_name && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>

                  <div className="p-2 ">
                    <label htmlFor="title" className="d-block">
                      Sector
                    </label>
                    {this.state.select_sector === false ? (
                      <input
                        type="text"
                        readOnly
                        value={this.state.sector_name}
                        className={`font-bold input-white w-20 inline-block  ${
                          this.state.error?.target === ErrorTarget.sector_name
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder=""
                        onClick={this.openSectorModal}
                        disabled={
                          this.state.district_name === "" ? true : false
                        }
                        required
                      />
                    ) : (
                      <FormSelectCustom
                        click={(data: SectorLocationItem) => {
                          this.setState({
                            select_sector: false,
                            sector_name: data.sector_name,
                            sector_id: data.sector_code,
                            select_province: false,
                            select_district: false,
                          });
                        }}
                        data={this.state.all_locations.sectors.filter(
                          (itm) => itm.district_code === this.state.district_id
                        )}
                        close={() => {
                          this.setState({
                            select_sector: false,
                          });
                        }}
                        id="sector_code"
                        title="sector_name"
                        loading={this.state.loading_location}
                      />
                    )}
                    {this.state.error?.target === ErrorTarget.sector_name && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>
                </div>
              )}

              <div className="p-2">
                <button
                  className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                  disabled={this.state.loading}
                  type="submit"
                >
                  {this.state.loading === true ? (
                    <b className="animate__animated animate__fadeIn animate__infinite">
                      Updating profile...
                    </b>
                  ) : (
                    "Update profile"
                  )}
                </button>
              </div>
            </form>
          </div>
        </Modal>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const EditProfileDetails = connect(mapStateToProps, {
  FC_UpdateProfile,
})(_EditProfileDetails);
